<template>
    <transition :name="transitionEffect">
        <div class="carousel-item" 
        v-show="currentSlide === index" 
        @mouseenter="$emit('mouseenter')" 
        @mouseout="$emit('mouseout') ">
            <!-- <img :src="slide" /> -->
            <h3>{{slide.name}}</h3>
            <p><span class="year">{{slide.year}}</span><span class="techno">{{slide.techno}}</span></p>
            <img :src="getImgUrl(slide.img)">
        </div>
    </transition>
</template>

<script>
export default
{
    emits: ['mouseenter', 'mouseout'],
    props: ["slide", "currentSlide", "index", "direction"],
    computed:
    {
        transitionEffect()
        {
            return this.direction ==="right" ? "slide-out" : "slide-in"
        }
    },
    methods:
    {
        getImgUrl(pic) 
        {
            return require('../../assets/img/'+pic)
        }       
    }
}
</script>

<style scoped>
*{font-family: Cambria;}
.carousel-item
{
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-image: url("../../assets/img/bg13.jpg");
    background-size: cover;
}
.slide-in-enter-active,
.slide-in-leave-active,
.slide-out-enter-active,
.slide-out-leave-active
{
    transition: all 1s ease-in-out;
}
.slide-in-enter-from
{
    transform: translateX(-100%)
}
.slide-in-leave-to
{
    transform: translateX(100%)
}
.slide-out-enter-from
{
    transform: translateX(100%)
}
.slide-out-leave-to
{
    transform: translateX(-100%)
}
h3
{
    color: white;
    font-size: 50px;
    font-weight: normal;
    text-align: center;
    margin-bottom: 0;
}
p
{
    color: white;
    font-size: 30px;
}
.year
{
    position: absolute;
    right: 30px;
    bottom: 30px;
    color: white;
}
.techno
{
    position: absolute;
    left: 30px;
    bottom: 30px;
    color:white;
}
img
{
    max-height: 60%;
    display: block;
    margin-left: auto;
    margin-right: auto;
    align-items: center;
}
@media only screen and (max-width: 900px) 
{
    h3
    {
        font-size: 20px;
    }
    img
    {
        width: 80%;
        margin: auto;
    }
}

</style>