<template>
    <div class="carousel-indicators">
        <button 
        class="carousel-indicator-item" 
        :class="{active: currentIndex === index}"
        v-for="(item, index) in total" 
        :key="index"
        @click="$emit('switch', index)"
        ></button>
    </div>
</template>

<script>
    export default{
        props: ["total", "currentIndex"],
        emits: ['switch']
    }
</script>


<style>
.carousel-indicators
{
    position: absolute;
    transform: translateX(-50%);
    left: 50%;
    bottom: 10px;
    z-index: 2;
}
.carousel-indicator-item
{
    width: 15px;
    height: 15px;
    border: none;
    background: #FFF;
    margin: 0.2em;
    opacity: .5;
    border-radius: 50%;
    cursor: pointer;
}
.active
{
    opacity: 1;
}
</style>