<template>
    <button class="carousel-control left" @click="$emit('prev')"><img src="../../assets/img/previous.png"></button>
    <button class="carousel-control right" @click="$emit('next')"><img src="../../assets/img/next.png"></button>
</template>

<script>
export default
{
    emits: ['prev', 'next']
}
</script>
 

<style scoped>
.carousel-control
{
    background-color: rgba(255, 255, 255, 0.5);
    padding-left: 10px;
    padding-right: 10px;
    border: none;
    display: inline-block;
    position: absolute;
    height: 50px;
    width: 50px;
    top: calc(50% - 25px);
    color: #f3f3f3;
    cursor: pointer;
    font-size: x-large;
    transition: all 0.2s ease-in-out;
} 
.carousel-control:hover
{
    height: 80px;
}
img
{
    width: 40px;
    display: block;
    margin-top: auto;
    margin-bottom: auto;
}
.carousel-control:hover
{
    background-color: rgba(255, 255, 255, 0.8);
    color: black;
} 
.left
